$primary-color: #f9f9f9;
$secondary-color: #3498db; // A cool blue color
$accent-color: #e74c3c;
$cool-text-color: #2c3e50; // A deep blue color for text
$fancy-text-color: #c0392b; // A fancy red color for text
$heading-font: 'Helvetica', sans-serif;
$body-font: 'Arial', sans-serif;

body {
  background-color: #ecf0f1; // Light gray background
  font-family: $body-font;
}

.player-color-sign {
  border-radius: 50%;
  margin-right: 1rem;
  width: 1.5rem;
  height: 1.5rem;
}

.player-house {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #d7d1d1;
  border-radius: 10px;
  margin: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  //min-width: 180px;

  h2 {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 5px;
    font-size: 18px;
    color: $fancy-text-color;
    font-family: $heading-font;
  }

  h3 {
    width: 100%;
    margin: 5px;
    font-size: 16px;
    color: $fancy-text-color;
    padding-left: 2rem;
  }

  .icon-box {
    display: flex;
    justify-content: flex-end;
    width: 100%;

    .icon {
      font-size: 1.5rem;
      color: $accent-color;
      margin-left: 1rem;
      cursor: pointer;
      transition: color 0.3s ease;

      &:hover {
        color: darken($accent-color, 10%);
      }
    }
  }

  .settings-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;

    .name-input {
      padding: 0.5rem;
      width: 8rem;
      border: 1px solid $accent-color;
      border-radius: 5px;
      color: $cool-text-color;
      transition: border-color 0.3s ease;

      &:focus {
        outline: none;
        border-color: darken($accent-color, 10%);
      }
    }

    .icon {
      font-size: 1.8rem;
      color: $secondary-color;
      cursor: pointer;
      transition: color 0.3s ease;

      &:hover {
        color: darken($secondary-color, 10%);
      }
    }
  }
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: $body-font;

  .display {
    display: flex;
    justify-content: center;
    align-items: center;
    color: $secondary-color;
  }

  .container {
    display: flex;
    justify-content: center;

    .board {
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: $primary-color;
      border-radius: 10px;
      padding: 1rem;

      .row {
        display: flex;

        .cell {
          margin: 1px;
          background-color: $secondary-color;
          display: flex;
          align-items: center;
          justify-content: center;
          width: 2.5rem;
          height: 2.5rem;
          cursor: pointer;
          transition: background-color 0.3s ease;

          &:hover {
            background-color: darken($secondary-color, 10%);
          }

          .disk {
            display: none;
            justify-content: center;
            align-items: center;
            width: 80%;
            height: 80%;
            border-radius: 50%;
            pointer-events: none;
            color: $cool-text-color;
            font-weight: bold;
            font-size: 1.8rem;
            transition: background-color 0.3s ease;
          }
        }
      }
    }
  }

  footer {
    display: none;
  }

  .reset-button {
    background-color: $accent-color;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: darken($accent-color, 10%);
    }
  }
}

@media only screen and (max-width: 950px) {
  .App {
    .container {
      .player-house {
        display: none;
      }
    }

    footer {
      display: flex;
    }
  }
}